import React, { useState,useEffect } from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import { useHistory, useLocation } from "react-router-dom";
import GEditor from 'grapesjs-react';
import 'grapesjs/dist/css/grapes.min.css';
import grapesjs from 'grapesjs';
import { Button } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles';
import BlockUI from '../Component/BlockUI';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../../../_metronic/_assets/css/Builder.scss';
import CustomVariables from '../Component/CustomVariables';

export function Builder() {

	//document.body.classList.add('body-builder');
var empty = ``
var content = `
<table className="full" bgcolor="#FFFFFF" width="100%" border="0" align="center" cellpadding="0" cellspacing="0">
  <tr>
    <td background="/media/main/img/header-bg-1.png" bgcolor="#07325c" valign="top" style="background-size:cover; background-position:center;">
      <table align="center" border="0" cellpadding="0" cellspacing="0">
        <tr>
          <td width="600" align="center" style="font-size:0px;">
            <table width="90%" border="0" align="center" cellpadding="0" cellspacing="0">
              <tr>
                <td height="70"></td>
              </tr>
              <!-- logo -->
              <tr>
                <td align="center" style="line-height: 0px;"><img src="/media/main/img/logo-1.png" alt="logo" style="display:block; line-height:0px; font-size:0px; border:0px;" /></td>
              </tr>
              <!-- end logo -->
              <!--dash-->
              <tr>
                <td align="center">
                  <table align="center" width="50" border="0" cellspacing="0" cellpadding="0">
                    <tr>
                      <td height="30" style="border-bottom:2px solid #FF646A;"></td>
                    </tr>
                  </table>
                </td>
              </tr>
              <!--end dash-->
              <tr>
                <td height="20"></td>
              </tr>
              <!-- content -->
              <tr>
                <td style="font-family: 'Open Sans', Arial, sans-serif; font-size:14px; color:#ffffff; line-height:28px;">
                  <span label="content">
                    <p align="center">Designers, developers and creatives from all over the globe are responsible for the work you see across Mumara’s ecosystem.</p>
                  </span>
                </td>
              </tr>
              <!-- end content -->
              <tr>
                <td height="50"></td>
              </tr>
            </table>
          </td>
        </tr>
      </table>
    </td>
  </tr>
</table>
`
var content2 = `
<table className="full" bgcolor="#EEEEEE" width="100%" border="0" align="center" cellpadding="0" cellspacing="0">
  <tr>
    <td align="center" background="/media/main/img/header-bg-2.png" bgcolor="#3b3b3b" style="background-size:cover; background-position:auto;">
      <table className="table-inner" width="600" style="max-width: 600px;" border="0" align="center" cellpadding="0" cellspacing="0">
        <tr>
          <td height="50"></td>
        </tr>
        <tr>
          <td>
            <!--logo-->
            <table className="table-full" width="150" align="left" border="0" cellpadding="0" cellspacing="0">
              <tr>
                <td height="50" align="center" style="line-height: 0px; padding-left: 25px;padding-right: 25px;border-radius: 6px;" bgcolor="#db7560"><img src="/media/main/img/logo-3.png" alt="img" width="79" style="display:block; line-height:0px; font-size:0px; border:0px;" /></td>
              </tr>
            </table>
            <!--end logo-->
            <!--[if (gte mso 9)|(IE)]></td><td><![endif]-->
            <table width="10" align="left" border="0" cellpadding="0" cellspacing="0">
              <tr>
                <td height="15"></td>
              </tr>
            </table>
            <!--[if (gte mso 9)|(IE)]></td><td><![endif]-->
            <!--slogan-->
            <table className="table-full" width="430" border="0" align="right" cellpadding="0" cellspacing="0">
              <tr>
                <td width="450" height="50" align="center" bgcolor="#FFFFFF" style="border-radius: 6px;">
                  <table width="90%" align="center" border="0" cellpadding="0" cellspacing="0">
                    <tr>
                      <td height="50" align="center" style="font-family: 'Open Sans', Arial, sans-serif; color:#7f8c8d; font-size:14px;">
                        <div label="slogan">Our community brings ideas to life</div>
                      </td>
                    </tr>
                  </table>
                </td>
              </tr>
            </table>
            <!--end slogan-->
          </td>
        </tr>
        <tr>
          <td height="50"></td>
        </tr>
      </table>
      <table className="table-inner" width="600" style="max-width: 600px;" border="0" align="center" cellpadding="0" cellspacing="0">
        <tr>
          <td align="center">
            <table width="90%" align="center" border="0" cellpadding="0" cellspacing="0">
              <!-- headline -->
              <tr>
                <td align="center" style="font-family: 'Open Sans', Arial, sans-serif; font-size:44px; color:#ffffff;  text-transform:uppercase; letter-spacing:8px;font-weight: bold; line-height:35px;">
                  <div label="headline">MUMARA</div>
                </td>
              </tr>
              <!-- end headline -->
              <!--dash-->
              <tr>
                <td align="center">
                  <table align="center" width="30" border="0" cellpadding="0" cellspacing="0">
                    <tr>
                      <td height="25" style="border-bottom:4px solid #db7560;"></td>
                    </tr>
                  </table>
                </td>
              </tr>
              <!--end dash-->
              <tr>
                <td height="25"></td>
              </tr>
              <!-- subheadline -->
              <tr>
                <td style="font-family: 'Open Sans', Arial, sans-serif; font-size:14px; color:#ffffff; text-transform:uppercase; letter-spacing:4px;">
                  <span label="subheadline">
                    <p align="center">Powered by a Community of Millions.</p>
                  </span>
                </td>
              </tr>
              <!-- end subheadline -->
              <tr>
                <td height="70"></td>
              </tr>
            </table>
          </td>
        </tr>
      </table>
    </td>
  </tr>
</table>
`

	var files_arr = [
		{id: 'modules',name:'Header - 1',image:'/media/images/thumbnail.jpg',content:content,module_name:'Module'},
		{id: 'modules',name:'Header - 2',image:'/media/images/thumbnail2.jpg',content:content2,module_name:'Module'},
		{id: 'modules',name:'Header - 3',image:'/media/images/thumbnail.jpg',content:content,module_name:'Module'},
		{id: 'modules',name:'Header - 4',image:'/media/images/thumbnail2.jpg',content:content2,module_name:'Module'}
	]
	const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
	}));

	const history = useHistory();
	const location = useLocation();
	const classes = useStyles();

	function goBack() {
      history.goBack();
    }

	console.log("Builder Template: "+location.state.id);

	const [state, setState] = useState({
		loader:false
	});

	const loader = () => {
		toast.success("Campaign has been saved", {position: "bottom-center",autoClose: 3000});
		setState({loader:true});
		setTimeout(function(){
			history.push("/campaigns/all");
			setState({loader:false});
		}.bind(),3000); 
	}

	var updateBlocks = (editor,manager)=>  {

		var tempId =  location.state.id;

		if(tempId == 0) {
			editor.setComponents(empty);
			console.log("Blank Template" + empty)
		} else if(tempId == 1) {
			editor.setComponents(empty);
			editor.setComponents(content);
			console.log("1111" + content)
		} else if(tempId == 2) {
			editor.setComponents(empty);
			editor.setComponents(content2);
			console.log("2222" + content2)
		} else {
			editor.setComponents(empty);
			console.log("Empty" + empty)
		}

		//editor.setComponents(content);

	var blockManager = manager
	blockManager.add("container_div", {
		label: `<div className="gjs-fonts gjs-f-b1 gjs-block gjs-one-bg gjs-four-color-h" title="Container" id="container" draggable="true"><img src="/media/images/container.png" /><div className="gjs-block-label">Container</div></div>`,
		content: '<table className="container" width="600px" align="center"><tr height="160px"><td></td></tr><tr height="20px"><td></td></tr></table>' ,
		category: 'Block',
		order:0, // 1,2,3,4...
		attributes: {
		  title: 0,
		  class: "grapesjs-modules",
		}
	  });
	var blocksV = manager.getAll()


	const filtered = blocksV.filter(block => block.get('category') == '')
	var ids = filtered.map((item)=>item.attributes.id)
	
	// const bm = manager;

	blockManager.render([
		ids.forEach(element => {
			blockManager.get(element).set('category', 'Block')
		})

	])

	blockManager.getCategories().each(ctg => ctg.set('open', false))

	files_arr.map((item,i)=>{
		console.log(item.id);
			blockManager.add(i, {
				label: `<label>${item.name}</label><img src="${item.image}"> `,
				content:item.content ,
				category: { id:item.id,label:item.module_name,order:2,open: true},
				attributes: {
					title: i,
					class: "grapesjs-modules",
				}
			});
		})
	}

	
	
	return (<>
		<div className="row">

			<div className="col-md-12">
				<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
					<div className="alert-text">
						<span className="svg-icon menu-icon">
							<SVG src={toAbsoluteUrl("/media/svg/icons/Design/Difference.svg")}/>
						</span> 
						<span>
							<FormattedMessage id="PAGE.BUILDER.DESC" />
						</span>
						<span className="svg-icon menu-icon goBack" onClick={goBack}>
							<img src={toAbsoluteUrl("/media/images/back.png")} />
						</span>
					</div>
				</div>
			</div>
    			
			<div className="col-md-12">
				<div className="bldrButtonBlk">
					<Button variant="contained" color="primary" className={classes.button} onClick={loader}>Save</Button>
					<Button variant="contained" className={classes.button} onClick={goBack}>Cancel</Button>
					<CustomVariables />
				</div>
				<GEditor id="gjs"  
                 onInit={editor=>updateBlocks(editor,editor.BlockManager)} presetType="newsletter" />
			</div>

			{
				state.loader?
				<BlockUI />
				: <div> </div>
			}	
			<ToastContainer />				

		</div>
	</>);

}