import React, { Component } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);

let chart = am4core.create("chartdiv", am4charts.PieChart3D);
chart.hiddenState.properties.opacity = 0;
chart.responsive.enabled = true;

chart.data = [
  {
    country: "Lahore",
    users: 125987
  },
  {
    country: "Islamabad",
    users: 97274	  
  },
  {
    country: "Faisalabad",
    users: 75987
  },
  {
    country: "Multan",
    users: 64876
  },
  {
    country: "Gujrat",
    users: 59876
  }
];

chart.innerRadius = am4core.percent(40);
chart.depth = 120;

chart.legend = new am4charts.Legend();

let series = chart.series.push(new am4charts.PieSeries3D());
series.dataFields.value = "users";
series.dataFields.depthValue = "users";
series.dataFields.category = "country";
series.slices.template.cornerRadius = 5;
series.colors.step = 3;

export class CitiesChart extends Component {

	render() {
	    return (
	    	<div className={`card card-custom mb-10 top-cities`}>
			    <div className="card-header border-0 pt-5">
			        <h3 className="card-title font-weight-bolder ">Top 5 Cities</h3>
			        <div className="card-toolbar">
			          </div>
			    </div>
			    <div className="card-body d-flex flex-column">
			        <div className="flex-grow-1">
			          <div id="chartdiv" style={{ width: "100%", height: "400px", margin: "50px auto -70px" }}>
			          </div>
			        </div>
			    </div>
		    </div>
	    );
	}
}