var api= 'http://localhost:8000/api/businesses'
var addapi= 'http://localhost:8000/api/businesses/addBusiness'
var addBranchapi = 'http://localhost:8000/api/branches/addBranch'
var branchapi = 'http://localhost:8000/api/branches'
var getCountriesAPI= 'http://localhost:8000/api/home/countries'

export async function getBusinesses(user_id) {
      try {
        const response = await fetch(api,{
          method:"POST",
          headers: {
            "Content-Type":"application/json"
          },
          body: JSON.stringify({user_id:1})
        });  
        const data = await response.json()
        console.log(data);
        return ({status: response.status, body:data.businesses})
       
      } catch (error) {
      console.log(error)
    }

  }
export async function getBranches(user_id) {
      try {
        const response = await fetch(branchapi,{
          method:"POST",
          headers: {
            "Content-Type":"application/json"
          },
          body: JSON.stringify({business_id:31})
        });  
        const data = await response.json()
        return ({status: response.status, body:data.branches})
       
      } catch (error) {
      console.log(error)
    }

  }
export async function AddBusiness(payload) {
      try {
        const response = await fetch(addapi,{
          method:"POST",
          headers: {
            "Content-Type":"application/json"
          },
          body: JSON.stringify(payload)
        });  
        const data = await response.json()
        return ({status: response.status, body:data.businesses})
       
      } catch (error) {
        console.log(error)
      }
  }

  export async function addBranch(payload) {
    try {
      const response = await fetch(addBranchapi,{
        method:"POST",
        headers: {
          "Content-Type":"application/json"
        },
        body: JSON.stringify(payload)
      });  
      const data = await response.json()
      return ({status: response.status, body:data.businesses})
     
    } catch (error) {
      console.log(error)
    }
}


export async function getAllCountries() {
      try {
        const response = await fetch(getCountriesAPI); 
        const data = await response.json()
        return ({status: response.status, body:data.countries})
       
      } catch (error) {
      console.log(error)
    }

  }