import React ,{useState,useEffect} from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";

import clsx from 'clsx';
import PropTypes from 'prop-types';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterListIcon from '@material-ui/icons/FilterList';
import { useHistory, useLocation } from "react-router-dom";
import swal from 'sweetalert';
import {connect,useSelector,useDispatch} from 'react-redux'
import * as bredux from './_redux/businessRedux'

function createData(name, subscribers, area, created_on, action) {
  return { name, subscribers, area, created_on, action };
}

const rows = [
  createData('Mall Branch', 7654, "Mall Road Lahore", "August 15, 2020", "Action"),
  createData('DHA III', 5432, "DHS Y Block Lahore", "August 11, 2020", "Action"),
  createData('Amana Mall', 4212, "Model Town Lahore", "August 07, 2020", "Action"),
  createData('MM ALAM Branch', 3974, "MM ALAM Road Lahore", "July 27, 2020", "Action"),
  createData('Johar Town', 2763, "Johar Town Lahore", "July 21, 2020", "Action"),
  createData('Wapda Town', 1902, "Wapda Town Lahore", "July 03, 2020", "Action"),
];

function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function stableSort(array, cmp) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === 'desc' ? (a, b) => desc(a, b, orderBy) : (a, b) => -desc(a, b, orderBy);
}

const headRows = [
  { id: 'name', numeric: false, disablePadding: true, label: 'Branch Name' },
  { id: 'subscribers', numeric: true, disablePadding: false, label: 'Subscribers' },
  { id: 'area', numeric: true, disablePadding: false, label: 'Area' },
  { id: 'created_on', numeric: true, disablePadding: false, label: 'Created On' },
  { id: 'action', numeric: true, disablePadding: false, label: 'Action' },
];

function EnhancedTableHead(props) {
  const { onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort } = props;
  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {headRows.map(row => (
          <TableCell
            key={row.id}
            align={row.numeric ? 'right' : 'left'}
            padding={row.disablePadding ? 'none' : 'default'}
            sortDirection={orderBy === row.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === row.id}
              direction={order}
              onClick={createSortHandler(row.id)}
            >
              {row.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const useToolbarStyles = makeStyles(theme => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  spacer: {
    flex: '1 1 100%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
}));

const EnhancedTableToolbar = props => {
  const classes = useToolbarStyles();
  const { numSelected } = props;

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      <div className={classes.title}>
        {numSelected > 0 ? (
          <Typography color="inherit" variant="subtitle1">
            {numSelected} selected
          </Typography>
        ) : (
          <Typography variant="h6" id="tableTitle">
            My Business - Khaadi - <span>Branches</span>
          </Typography>
        )}
      </div>
      <div className={classes.spacer} />
      <div className={classes.actions}>
        {numSelected > 0 ? (
          <Tooltip title="Delete">
            <IconButton aria-label="Delete">
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title="Filter list">
            <IconButton aria-label="Filter list">
              <FilterListIcon />
            </IconButton>
          </Tooltip>
        )}
      </div>
    </Toolbar>
  );
};

EnhancedTableToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired,
};

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(3),
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: 750,
  },
  tableWrapper: {
    overflowX: 'auto',
  },
}));

 function Branches(props) {

  const history = useHistory();
  const location = useLocation();
  function viewbranch() {
      history.push("/business/branch/view");
  }
  function branchSubscribers() {
      history.push("/business/branch/subscriber");
  }
  function goBack() {
    history.goBack();
  }
  function viewOffers() {
    history.push("/business/branch/offers");
  }


    const globalState  = useSelector(state=>state.business)
    const [allbranches, setdata] = useState([]);

		useEffect( ()=>{
      //console.log(globalState);
			setdata(globalState.all_branches)
		});

		useEffect( ()=>{
      props.getBranches();
		} , []);


	  const classes = useStyles();
	  const [order, setOrder] = React.useState('desc');
	  const [orderBy, setOrderBy] = React.useState('subscribers');
	  const [selected, setSelected] = React.useState([]);
	  const [page, setPage] = React.useState(0);
	  const [rowsPerPage, setRowsPerPage] = React.useState(5);

	  function handleRequestSort(event, property) {
	    const isDesc = orderBy === property && order === 'desc';
	    setOrder(isDesc ? 'asc' : 'desc');
	    setOrderBy(property);
	  }

	  function handleSelectAllClick(event) {
	    if (event.target.checked) {
	      const newSelecteds = rows.map(n => n.name);
	      setSelected(newSelecteds);
	      return;
	    }
	    setSelected([]);
	  }

	  function handleClick(event, name) {
	    const selectedIndex = selected.indexOf(name);
	    let newSelected = [];

	    if (selectedIndex === -1) {
	      newSelected = newSelected.concat(selected, name);
	    } else if (selectedIndex === 0) {
	      newSelected = newSelected.concat(selected.slice(1));
	    } else if (selectedIndex === selected.length - 1) {
	      newSelected = newSelected.concat(selected.slice(0, -1));
	    } else if (selectedIndex > 0) {
	      newSelected = newSelected.concat(
	        selected.slice(0, selectedIndex),
	        selected.slice(selectedIndex + 1),
	      );
	    }

	    setSelected(newSelected);
	  }

	  function handleChangePage(event, newPage) {
	    setPage(newPage);
	  }

	  function handleChangeRowsPerPage(event) {
	    setRowsPerPage(+event.target.value);
	  }

	  const isSelected = name => selected.indexOf(name) !== -1;

	  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

    /*function deleteBranch() {
      swal({
          title: "Are you sure?",
          text: "Once deleted, you will not be able to recover this subscribers!",
          icon: "warning",
          buttons: true,
          dangerMode: true,
        })
        .then((willDelete) => {
          if (willDelete) {
            swal("Branch has been deleted!", {
              icon: "success",
            });
        } else {
            swal("Branch and Subscribers are safe!");
        }
      });
    }*/

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
          	<SVG src={toAbsoluteUrl("/media/svg/icons/Home/Building.svg")}/>
        	</span> 
					<span>
						<FormattedMessage id="PAGE.BRANCHES.DESC" /> 
					</span>
          <span className="svg-icon menu-icon goBack" onClick={goBack}>
						<i className="fa fa-long-arrow-alt-left"></i>
			    </span>
				</div>
			</div>

			<div className={`mt-0 branches-table ` + classes.root}>
        <Paper className={classes.paper}>
        <Toolbar>
          <div className={classes.title}>
              <Typography variant="h6" id="tableTitle">
                My Business - Khaadi - <span>Branches</span>
              </Typography>
          </div>
        </Toolbar>
          <div className={classes.tableWrapper}>
            <Table
              className={`table-center `+ classes.table}
              aria-labelledby="tableTitle"
            >
              <EnhancedTableHead
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={handleSelectAllClick}
                onRequestSort={handleRequestSort}
                rowCount={allbranches.length}
              />
              <TableBody>
                {stableSort(allbranches, getSorting(order, orderBy))
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row, index) => {
                    const isItemSelected = isSelected(row.name);
                    const labelId = `enhanced-table-checkbox-${index}`;

                    return (
                      <TableRow
                        hover
                        role="checkbox"
                        aria-checked={isItemSelected}
                        tabIndex={-1}
                        key={row.name}
                      >
                        <TableCell align="left" component="th" id={labelId} scope="row" padding="none">
                          <div className="text-link text-primary" onClick={viewbranch}>{row.name}</div>
                        </TableCell> 
                        <TableCell align="left"><div className="text-link text-primary" onClick={branchSubscribers}>{row.total_subscribers}</div></TableCell>
                        <TableCell align="left">{row.city}</TableCell>
                        <TableCell align="left">{row.created_at}</TableCell>
                        <TableCell align="left">
                          <button onClick={viewbranch} className="btn btn-icon btn-light btn-hover-primary btn-sm mx-1 eye">
                              <span className="svg-icon svg-icon-md svg-icon-primary">
                                <SVG src={toAbsoluteUrl("/media/svg/icons/General/EYE.svg")} />
                              </span>
                          </button>
                          <a href="/media/images/qrcode.png" download className="btn btn-icon btn-light btn-hover-success btn-sm mx-1 qrcode">
                              <span className="svg-icon svg-icon-md svg-icon-success">
                                <SVG src={toAbsoluteUrl("/media/svg/icons/General/QR.svg")} />
                              </span>
                          </a>
                          <button className="btn btn-icon btn-light btn-hover-danger btn-sm mx-1 announce" onClick={viewOffers}>
                              <span className="svg-icon svg-icon-md svg-icon-danger">
                                <SVG src={toAbsoluteUrl("/media/svg/icons/General/Announce.svg")} />
                              </span>
                          </button>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                {emptyRows > 0 && (
                  <TableRow style={{ height: 49 * emptyRows }}>
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </div>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={allbranches.length}
            rowsPerPage={rowsPerPage}
            page={page}
            backIconButtonProps={{
              'aria-label': 'Previous Page',
            }}
            nextIconButtonProps={{
              'aria-label': 'Next Page',
            }}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </Paper>
      </div>

		</div>
  </>);
  

}

export default connect(null, bredux.actions)(Branches);