import React, { useState, useEffect } from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import { useHistory } from "react-router-dom";

import { makeStyles, useTheme } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import swal from 'sweetalert';
import { useFormik } from "formik";
import * as Yup from "yup";

import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Chip from '@material-ui/core/Chip';

import {SheduleRepeater} from '../Component/SheduleRepeater';

export function CreateSchedule(props) {

	const theme = useTheme();

	const initialValues = {
		business: "",
		name: "",
		branches: "",
		campaign: "",
	};

	const history = useHistory();

	function goBack() {
      history.goBack();
	}
	
	const [loading, setLoading] = useState(false);

  	const BranchSchema = Yup.object().shape({
    	name: Yup.string()
    		.min(4, "Minimum 4 characters require")
      		.max(50, "Maximum 50 characters require")
	      	.required("Enter a branch name"),
		campaign: Yup.string()
			.required("Select Campaign"),
		business: Yup.string()
			.required("Select Business"),
	});

	const enableLoading = () => {
    	setLoading(true);
	};

	  const disableLoading = () => {
	    setLoading(false);
	};

	const getInputClasses = (fieldname) => {
	    if (formik.touched[fieldname] && formik.errors[fieldname]) {
	      return "is-invalid";
	    }

	    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
	      return "is-valid";
	    }

	    return "";
	};

	const formik = useFormik({
    	initialValues,
    	validationSchema: BranchSchema,
    	onSubmit: (values, { setStatus, setSubmitting }) => {
    		setStatus("Campaign Shedule successfully");
    		console.log(`
    			Schedule Name: ${values.name}. 
    			Business Name: ${values.business}. 
    			Branches: ${values.branches}. 
				Campaign Name: ${values.campaign}. 
				Condition: ${values.option} ${values.condition} ${values.gender}
    		`);
    		setTimeout(() => {
		      	swal({
					  title: "Scheduled Campaign",
					  text: "Campaign Shedule successfully!",
					  icon: "success",
					  button: null,
					  timer: 3000
				});
			}, 100);
			setTimeout(() => {
			  history.push({
			  	pathname: '/campaigns/schedule/all',
			  });
			}, 3500);
	    },
	});
	
	const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
		chips: {
			display: 'flex',
			flexWrap: 'wrap',
		},
		chip: {
			margin: 2,
		},
		noLabel: {
			marginTop: theme.spacing(3),
		},
	}));

	const ITEM_HEIGHT = 48;
	const ITEM_PADDING_TOP = 8;
	const MenuProps = {
		PaperProps: {
			style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
			},
		},
	};

	const branches = [
		'MM Alam Branch',
		'Mall Road Branch',
		'Ichra Branch',
		'Gullberg Branch',
		'Johar Town Branch',
		'Wapda Town Branch',
		'Shahdra Branch',
		'Jail Road Branch',
		'Shadbagh Branch',
		'Yadgaar Branch',
	];

	function getStyles(name, personName, theme) {
		return {
		  fontWeight:
			personName.indexOf(name) === -1
			  ? theme.typography.fontWeightRegular
			  : theme.typography.fontWeightMedium,
		};
	}

	const [personName, setPersonName] = React.useState([]);
	function handleChange(event) {
		setPersonName(event.target.value);
		setValues({branches: event.target.value});
	}
	function handleChangeMultiple(event) {
		const { options } = event.target;
		const value = [];
		for (let i = 0, l = options.length; i < l; i += 1) {
		  if (options[i].selected) {
			value.push(options[i].value);
		  }
		}
		setPersonName(value);
	}

	const classes = useStyles();
	const [values, setValues] = React.useState({
		name: '',
		business:'',
		branches:'',
		campaign:''
	});

	const [state, setState] = React.useState({
	    showOption: true,
		name: '',
		business:'',
		branches:'',
		campaign:'',
		option: "",
		condition: "",
		gender: ""
	});

	const business = [
		{
		value: 'Khaadi',
		label: 'Khaadi',
		},
		{
		value: 'Sapphire',
		label: 'Sapphire',
		},
		{
		value: 'McDownlad',
		label: 'McDownlad',
		},
		{
		value: 'KFC',
		label: 'KFC',
		},
		{
		value: 'Bonanza',
		label: 'Bonanza',
		}
	];

	const campaign = [
		{
		value: 'Public Holiday',
		label: 'Public Holiday',
		},
		{
		value: 'Independance Day',
		label: 'Independance Day',
		},
		{
		value: 'Defence Day',
		label: 'Defence Day',
		},
		{
		value: 'Eid Specials',
		label: 'Eid Specials',
		},
		{
		value: 'Promo 50%',
		label: 'Promo 50%',
		}
	];

	const hadleOptionChange = (event) => {
		event.preventDefault();
		setState({
			showOption : true
		});
	 }

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
                    	<SVG src={toAbsoluteUrl("/media/svg/icons/Design/Difference.svg")}/>
                  	</span> 
					<span>
						<FormattedMessage id="PAGE.ADDSCHEDULED.DESC" />
					</span>
			        <span className="svg-icon menu-icon goBack" onClick={goBack}>
			            <img src="/media/images/back.png" />
			        </span>
				</div>
			</div>

			<div className="card card-custom gutter-b col-md-12 innerBlks schedule-campaign-page">
				<div className="card-header">
					<div className="card-title"><h3 className="card-label">Create Scheduled Campaign</h3></div>
				</div>
		        <div className="card-body">
		        	
	        		<div className="row">
		                <div className="col-md-6 ffield">
		                	<form 
				        		autoComplete="off"
						        onSubmit={formik.handleSubmit}
						        className="form fv-plugins-bootstrap fv-plugins-framework"
				        	>
				        		{formik.status ? (
						          <div className="mb-10 alert alert-custom alert-light-success alert-dismissible">
						            <div className="alert-text font-weight-bold">{formik.status}</div>
						          </div>
						        ) : (
						          <div>
						          </div>
						        )}

								<div className="fieldBlk">
				                    <TextField
				                        name="name"
				                        label="Shedule Name"
				                        type="text"
				                        className={`form-control h-auto py-0 px-0  ${getInputClasses(
							              "name"
							            )}`}
				                        variant="outlined"
				                        value={values.name}
	        							{...formik.getFieldProps("name")}
				                    />
				                    {formik.touched.name && formik.errors.name ? (
							            <div className="fv-plugins-message-container">
							              <div className="fv-help-block">{formik.errors.name}</div>
							            </div>
							          ) : null}
				                    <p className="MuiFormHelperText-root MuiFormHelperText-contained">
										Shedule Name Helping Text
				                    </p>
			                    </div>
								
						        <div className="fieldBlk">
									<TextField
				                        select
				                        name="business"
				                        variant="outlined"
										onChange={(event) => hadleOptionChange(event)}
										label="Select Business"
				                        className={`form-control h-auto py-0 px-0  ${getInputClasses(
							              "business"
										)}`}
										value={values.business}
										{...formik.getFieldProps("business")}
				                    >
				                        {business.map(option => (
				                          <MenuItem key={option.value} value={option.value}>
				                            {option.label}
				                          </MenuItem>
				                        ))}
				                    </TextField>
									{formik.touched.business && formik.errors.business ? (
							            <div className="fv-plugins-message-container">
							              <div className="fv-help-block">{formik.errors.business}</div>
							            </div>
							          ) : null}
				                    <p className="MuiFormHelperText-root MuiFormHelperText-contained">
				                    	Select Business Helping Text
				                    </p>
						        </div>

								{
									state.showOption?
									<div className="form-group">
										<div className="fieldBlk">
											<FormControl className={classes.formControl}>
												<InputLabel htmlFor="select-multiple-chip">Select Branches</InputLabel>
												<Select
												multiple
												variant="outlined"
												name="branches"
												onChange={handleChange}
												input={<Input id="select-multiple-chip" />}
												renderValue={selected => (
													<div className={classes.chips}>
													{selected.map(value => (
														<Chip key={value} label={value} className={classes.chip} />
													))}
													</div>
												)}
												MenuProps={MenuProps}
												value={personName}
												>
												{branches.map(name => (
													<MenuItem key={name} value={name} style={getStyles(name, personName, theme)}>
													{name}
													</MenuItem>
												))}
												</Select>
											</FormControl>
											<p className="MuiFormHelperText-root MuiFormHelperText-contained">
												Select Branches Helping Text
											</p>
										</div>
									</div>
									: <div> </div>
								}
								
								
								
								<div className="fieldBlk">
									<TextField
										select
										name="campaign"
										variant="outlined"
										label="Select Campaign"
										className={`form-control h-auto py-0 px-0  ${getInputClasses("campaign")}`}
										
										{...formik.getFieldProps("campaign")}
									>
										{campaign.map(option => (
										<MenuItem key={option.value} value={option.value}>
											{option.label}
										</MenuItem>
										))}
									</TextField>
									{formik.touched.campaign && formik.errors.campaign ? (
							            <div className="fv-plugins-message-container">
							              <div className="fv-help-block">{formik.errors.campaign}</div>
							            </div>
							          ) : null}
									<p className="MuiFormHelperText-root MuiFormHelperText-contained">
										Select campaign Helping Text
									</p>
								</div>

								<SheduleRepeater />
								
								<div className="class-submit clear">
									<Button 
										type="submit"
										disabled={formik.isSubmitting}
										variant="contained" 
										color="primary" 
										className={classes.button}
									>
										Save 
										{loading && <span className="ml-3 spinner spinner-white"></span>}
									</Button>
									<Button variant="contained" className={`ml-2 `+classes.button}>
										Cancel
									</Button>
								</div>

	  					    </form>
		                </div>
		                <div className="col-md-6 text-center">
		                	<img src="/media/images/schedule.jpg" />
		                </div> 
	                </div>						    
				      
		        </div>
		    </div>    


		</div>
	</>);

}