import React, { Component } from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom";
import swal from 'sweetalert';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


export function ViewBranch() {

	const history = useHistory();
	function goBack() {
		history.goBack();
	}

	const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
	}));

	const classes = useStyles();
	const [values, setValues] = React.useState({
		addresses: '257 F Block MM Alam Road',
	    address: '',
	});

	const handleChange = name => event => {
	    setState({ ...state, [name]: event.target.value, });
        setValues({ ...values, [name]: event.target.value });
	};

  	const [state, setState] = React.useState({
  		show: true,
	});

  	function deleteBusiness() {
  		swal({
          title: "Are you sure?",
          text: "Branch has attached many subscribers. it will be remove all subscribers!",
          icon: "error",
          buttons: true,
          dangerMode: true,
        })
        .then((willDelete) => {
          if (willDelete) {
            swal("Branch has been deleted!", {
              icon: "success",
            });
            setTimeout(() => {
			  history.push({
			  	pathname: '/business/branches',
			  });
			}, 1500);
        } else {
            swal("Delete Branch Cancel");
        }
      });
	}

	function saveData() {
		setState({show:true});
		var dv = values.address;
		var save = setValues({addresses: dv});
		toast.success("Address successfully changed", {position: "bottom-center",autoClose: 3000});  
	}

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
                    	<SVG src={toAbsoluteUrl("/media/svg/icons/Home/Building.svg")}/>
                  	</span> 
					<span>
						<FormattedMessage id="PAGE.BRANCHDETAILS.DESC" />
					</span>
					<span className="svg-icon menu-icon goBack" onClick={goBack}>
						<i className="fa fa-long-arrow-alt-left"></i>
			    	</span>
				</div>
			</div>

			<div className="card card-custom gutter-b col-md-12 buss-details">
				<div className="card-header">
					<div className="card-title"><h3 className="card-label">Khaadi - Branch Details</h3></div>
					{
			          state.show?
			          <Button variant="contained" className="Business-edit"  onClick={()=>(setState({show:false}))} > Edit </Button>
			          :
			          <div>
			          	<Button variant="contained" className="Business-edit" color="secondary"  onClick={saveData} > Save </Button>
			          	<Button variant="contained" className="Business-edit" onClick={()=>(setState({show:true}))} > Cancel </Button>
			          </div>
			        }
					
				</div>
		        <div className="card-body">

		        	<div className="row">
		        		<div className="col-md-6">
		        			<div className="viewBlk">
		        				<label htmlFor="name">Branch Name</label>
			        			<div className="detail">MM Alam Road Lahore</div>
		        			</div>
			        		<div className="viewBlk">
		        				<label htmlFor="name">Branch Address</label>
				        			{
							          state.show?
							          <div className="detail">{values.addresses}</div>
							          :<div className="addrBlock">
					        				<TextField
						                      required
						                        id="address"
						                        name="address"
						                        type="address"
						                        defaultValue={values.addresses}
						                        className={classes.textField}
						                        onChange={e => setValues({address: e.target.value})}
						                        placeholder="Branch Address"
						                        margin="normal"
						                        variant="outlined"
			        							helperText="Change Branch Address Help Text"
						                    />
				        				</div>
							        }
		        			</div>	
			        		<div className="viewBlk">
		        				<label htmlFor="name">Branch City</label>
		        				<div className="detail">Lahore</div>
		        			</div>	
			        		<div className="viewBlk">
		        				<label htmlFor="name">Branch Created On</label>
		        				<div className="detail">21-07-2020</div>
		        			</div>	
			        		<div className="viewBlk">
		        				<label htmlFor="name">Engagements</label>
		        				<div className="detail">156309</div>
		        			</div>	
		        		</div>
		        		<div className="col-md-6">
		        			<div className="viewBlk">
		        				<label htmlFor="name">Branch Address</label>
		        				<div className="detail">257 F Block MM Alam Road</div>
		        			</div>
			        		<div className="viewBlk">
		        				<label htmlFor="name">Branch Area</label>
	        					<div className="detail">Gullberg III</div>
		        			</div>	
			        		<div className="viewBlk">
		        				<label htmlFor="name">Branch Country</label>
		        				<div className="detail">Pakistan</div>
		        			</div>	
			        		<div className="viewBlk">
		        				<label htmlFor="name">Number of Subscribers</label>
		        				<div className="detail">98564</div>
		        			</div>	
			        		<div className="viewBlk">
		        				
		        			</div>	
		        		</div>
		        		{
				          state.show?
				          null
				          :<div className="col-md-12">
			        			<div className="detail text-right">
			        				<a href="#" className="text-danger" onClick={deleteBusiness}>- Permanently Remove Branch</a>
			        			</div>
			        		</div>
				        }	
		        	</div>
		        	<ToastContainer />
		        </div>
		    </div>  

		</div>
	</>);

}