import React from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";

import Button from '@material-ui/core/Button';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterListIcon from '@material-ui/icons/FilterList';
import { useHistory } from "react-router-dom";
import swal from 'sweetalert';


function createData(name, group, subject, created_on, action) {
  return { name, group, subject, created_on, action };
}

const rows = [
  createData('Public Holiday', "Holiday", "Good Friday Gifts", "August 15, 2020", "Action"),
  createData('Independance Day', "Promo", "14 August Offer", "August 11, 2020", "Action"),
  createData('Defence Day', "Staff Announcement", "6 September Sale", "August 07, 2020", "Action"),
  createData('Eid Specials', "Special Discount", "Eidi Discount", "July 27, 2020", "Action"),
  createData('Promo 50%', "Discount", "Buy 1 Get 1", "July 21, 2020", "Action"),
  createData('Sale upto 70%', "Sale", "Hungama Sale", "July 03, 2020", "Action"),
];

function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function stableSort(array, cmp) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === 'desc' ? (a, b) => desc(a, b, orderBy) : (a, b) => -desc(a, b, orderBy);
}

const headRows = [
  { id: 'name', numeric: false, disablePadding: true, label: 'Name' },
  { id: 'group', numeric: true, disablePadding: false, label: 'Group' },
  { id: 'subject', numeric: true, disablePadding: false, label: 'Subject' },
  { id: 'created_on', numeric: true, disablePadding: false, label: 'Created On' },
  { id: 'action', numeric: true, disablePadding: false, label: 'Action' },
];

function EnhancedTableHead(props) {
  const { onSelectAllClick, order, orderBy,  rowCount, onRequestSort } = props;
  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {headRows.map(row => (
          <TableCell
            key={row.id}
            align={row.numeric ? 'right' : 'left'}
            padding={row.disablePadding ? 'none' : 'default'}
            sortDirection={orderBy === row.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === row.id}
              direction={order}
              onClick={createSortHandler(row.id)}
            >
              {row.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const useToolbarStyles = makeStyles(theme => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  spacer: {
    flex: '1 1 100%',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
}));

const EnhancedTableToolbar = props => {
  const classes = useToolbarStyles();
  const { numSelected } = props;

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      <div className={classes.title}>
          <Typography variant="h6" id="tableTitle">
            Broadcasts List
          </Typography>
      </div>
      <div className={classes.spacer} />
    </Toolbar>
  );
};

EnhancedTableToolbar.propTypes = {
  numSelected: PropTypes.number.isRequired,
};

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(3),
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: 750,
  },
  tableWrapper: {
    overflowX: 'auto',
  },
}));


export function Broadcasts() {

	const history = useHistory();
	function editCampaign() {
	    history.push("/campaigns/add");
	}
	function addCampaign() {
	    history.push("/campaigns/new");
	}
	function importTemplate() {
	    history.push("/campaigns/my-templates");
	}


	  const classes = useStyles();
	  const [order, setOrder] = React.useState('desc');
	  const [orderBy, setOrderBy] = React.useState('index');
	  const [selected, setSelected] = React.useState([]);
	  const [page, setPage] = React.useState(0);
	  const [rowsPerPage, setRowsPerPage] = React.useState(5);

	  function handleRequestSort(event, property) {
	    const isDesc = orderBy === property && order === 'desc';
	    setOrder(isDesc ? 'asc' : 'desc');
	    setOrderBy(property);
	  }

	  function handleSelectAllClick(event) {
	    if (event.target.checked) {
	      const newSelecteds = rows.map(n => n.name);
	      setSelected(newSelecteds);
	      return;
	    }
	    setSelected([]);
	  }

	  function handleClick(event, name) {
	    const selectedIndex = selected.indexOf(name);
	    let newSelected = [];

	    if (selectedIndex === -1) {
	      newSelected = newSelected.concat(selected, name);
	    } else if (selectedIndex === 0) {
	      newSelected = newSelected.concat(selected.slice(1));
	    } else if (selectedIndex === selected.length - 1) {
	      newSelected = newSelected.concat(selected.slice(0, -1));
	    } else if (selectedIndex > 0) {
	      newSelected = newSelected.concat(
	        selected.slice(0, selectedIndex),
	        selected.slice(selectedIndex + 1),
	      );
	    }

	    setSelected(newSelected);
	  }

	  function handleChangePage(event, newPage) {
	    setPage(newPage);
	  }

	  function handleChangeRowsPerPage(event) {
	    setRowsPerPage(+event.target.value);
	  }

	  const isSelected = name => selected.indexOf(name) !== -1;

	  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

	function deleteBroadcast() {
      swal({
          title: "Are you sure?",
          text: "Once deleted, Broadcast will be delete!",
          icon: "warning",
          buttons: true,
          dangerMode: true,
        })
        .then((willDelete) => {
          if (willDelete) {
            swal("Broadcast has been deleted!", {
              icon: "success",
            });
        } else {
            swal("Broadcast is safe!");
        }
      });
    }

    function goBack() {
	    history.goBack();
	}

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
                    	<SVG src={toAbsoluteUrl("/media/svg/icons/Design/Difference.svg")}/>
                  	</span> 
					<span>
						<FormattedMessage id="PAGE.BROADCASTS.DESC" />
					</span>
			        <span className="svg-icon menu-icon goBack" onClick={goBack}>
			            <img src="/media/images/back.png" />
			        </span>
				</div>
			</div>

			<div className="card card-custom gutter-b col-md-12">
		        <div className="card-body">

		        	<div className="toolbar">
		        		<div className="form-group">
							<Button variant="contained" color="primary" className={classes.button} onClick={addCampaign}>
						        Add Broadcast 
						    </Button>
				        	<Button variant="contained" color="secondary" className={classes.button} onClick={importTemplate}>
						        Import Template
						    </Button>
		        		</div>
		        	</div>

				    <div>
						 <div className={`campaigns-table ` + classes.root}>
					      <Paper className={classes.paper}>
					        <div className={classes.tableWrapper}>
					          <Table
					            className={classes.table}
					            aria-labelledby="tableTitle"
					          >
					            <EnhancedTableHead
					              order={order}
					              orderBy={orderBy}
					              onRequestSort={handleRequestSort}
					              rowCount={rows.length}
					            />
					            <TableBody>
					              {stableSort(rows, getSorting(order, orderBy))
					                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
					                .map((row, index) => {
					                  const isItemSelected = isSelected(row.name);
					                  const labelId = `enhanced-table-checkbox-${index}`;

					                  return (
					                    <TableRow
					                      hover
					                      role="checkbox"
					                      aria-checked={isItemSelected}
					                      tabIndex={-1}
					                      key={row.name}
					                    >
					                      <TableCell align="left" component="th" id={labelId} scope="row" padding="none">
					                        {row.name}
					                      </TableCell>
					                      <TableCell align="left">{row.group}</TableCell>
					                      <TableCell align="left">{row.subject}</TableCell>
					                      <TableCell align="left">{row.created_on}</TableCell>
					                      <TableCell align="left">
					                      	<button className="btn btn-icon btn-light btn-hover-primary btn-sm mx-3" onClick={addCampaign}>
		                                      <span className="svg-icon svg-icon-md svg-icon-primary">
		                                        <SVG src={toAbsoluteUrl("/media/svg/icons/Communication/Write.svg")} />
		                                      </span>
		                                    </button>
		                                	<button className="btn btn-icon btn-light btn-hover-danger btn-sm" onClick={deleteBroadcast}>
		                                      <span className="svg-icon svg-icon-md svg-icon-danger">
		                                        <SVG src={toAbsoluteUrl("/media/svg/icons/General/Trash.svg")} />
		                                      </span>
		                                    </button>
					                      </TableCell>
					                    </TableRow>
					                  );
					                })}
					              {emptyRows > 0 && (
					                <TableRow style={{ height: 49 * emptyRows }}>
					                  <TableCell colSpan={6} />
					                </TableRow>
					              )}
					            </TableBody>
					          </Table>
					        </div>
					        <TablePagination
					          rowsPerPageOptions={[5, 10, 25]}
					          component="div"
					          count={rows.length}
					          rowsPerPage={rowsPerPage}
					          page={page}
					          backIconButtonProps={{
					            'aria-label': 'Previous Page',
					          }}
					          nextIconButtonProps={{
					            'aria-label': 'Next Page',
					          }}
					          onChangePage={handleChangePage}
					          onChangeRowsPerPage={handleChangeRowsPerPage}
					        />
					      </Paper>
					    </div>
					</div>
				</div>		
			</div>

		</div>
	</>);

}