import React, { useState } from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';

import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom";
import swal from 'sweetalert';

import { useFormik } from "formik";
import * as Yup from "yup";

const initialValues = {
  docs: "",
  other: "",
};


export function BusinessVerify() {

	const [loading, setLoading] = useState(false);

  	const BusinessSchema = Yup.object().shape({
	    docs: Yup.string()
	      	.required("Registration Date require"),
	});

	const enableLoading = () => {
    	setLoading(true);
	};

	  const disableLoading = () => {
	    setLoading(false);
	};

	const getInputClasses = (fieldname) => {
	    if (formik.touched[fieldname] && formik.errors[fieldname]) {
	      return "is-invalid";
	    }

	    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
	      return "is-valid";
	    }

	    return "";
	};

	const formik = useFormik({
    	initialValues,
    	validationSchema: BusinessSchema,
    	onSubmit: (values: any, { setStatus, setSubmitting }) => {
    		setStatus("Business document uploaded successfully.");
    		console.log(`
    			docs: ${values.docs}. 
    			fileType: ${values.fileType}. 
    			other: ${values.other}
    		`);
    		setTimeout(() => {
		      	swal({
					  title: "Business Document",
					  text: "Document uploaded successfully. Your business will be verified after proof of business!",
					  icon: "success",
					  button: null,
					  timer: 2000
				});
				setTimeout(() => {
				  history.push({
				  	pathname: '/business/all',
				  });
				}, 2500);
			}, 100);
	    },
	});

	const history = useHistory();
	function viewBusiness() {
	    history.push("/business/all");
	}
	function goBack() {
	    history.goBack();
	}

	const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
	}));

	const classes = useStyles();
	const [values, setValues] = React.useState({
	    docs: '',
	    fileType: '',
	    other:''
	});

	const handleChange = name => event => {
      setValues({ ...values, [name]: event.target.value });
      setValues({ ...values, [name]: event.target.value });
	};

	const [state, setState] = React.useState({
  		value: "",
	    checkedA: true,
	});

	const fileType = [
    {
        value: 'company',
        label: 'Company Incorporation Certificate',
      },
      {
        value: 'other',
        label: 'Other Registration Document',
      }
  	];

  	const divstatus = (e) => {
    	setState({value: e.target.value});
    	console.log(state.value);
  	}

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
                    	<SVG src={toAbsoluteUrl("/media/svg/icons/Home/Building.svg")}/>
                  	</span> 
					<span>
						<FormattedMessage id="PAGE.VERIFYBUSINESS.DESC" />
					</span>
					<span className="svg-icon menu-icon goBack" onClick={goBack}>
						<i className="fa fa-long-arrow-alt-left"></i>
			    	</span>
				</div>
			</div>

			<div className="card card-custom gutter-b col-md-12 innerBlks">
				<div className="card-header">
					<div className="card-title"><h3 className="card-label">Upload Business Document</h3></div>
				</div>
		        <div className="card-body">
		        				
		        	<div className="row">
			            <div className="col-md-6 ffield">
							<form 
					        		autoComplete="off"
							        onSubmit={formik.handleSubmit}
							        className="ffield form fv-plugins-bootstrap fv-plugins-framework"
					        	>
					        		{formik.status ? (
							          <div className="mb-10 alert alert-custom alert-light-success alert-dismissible">
							            <div className="alert-text font-weight-bold">{formik.status}</div>
							          </div>
							        ) : (
							          <div>
							          </div>
							        )}

							    <div className="fieldBlk">
				                	<TextField
				                        id="docs"
				                        name="docs"
				                        type="file"
				                        placeholder=""
				                        variant="outlined"
				                        className={`form-control h-auto py-0 px-0  ${getInputClasses(
							              "docs"
							            )}`}
				                        value={values.docs}
	        							{...formik.getFieldProps("docs")}
				                    />
				                    {formik.touched.docs && formik.errors.docs ? (
							            <div className="fv-plugins-message-container">
							              <div className="fv-help-block">{formik.errors.docs}</div>
							            </div>
							          ) : null}
				                    <p className="MuiFormHelperText-root MuiFormHelperText-contained">
				                    	Upload the company verification documents (jpg, png, pdf)
				                    </p>
			                    </div>

			                    <div className="fieldBlk">
				                    <TextField
				                        select
				                        name="fileType"
				                        variant="outlined"
				                        label="Select File Type"
				                        className={`form-control h-auto py-0 px-0  ${getInputClasses(
							              "fileType"
							            )}`}
				                        value={values.fileType}
				                        {...formik.getFieldProps("fileType")}
				                        onChange={divstatus}
				                    >
				                        {fileType.map(option => (
				                          <MenuItem key={option.value} value={option.value}>
				                            {option.label}
				                          </MenuItem>
				                        ))}
				                    </TextField>
				                    <p className="MuiFormHelperText-root MuiFormHelperText-contained">
				                    	File Type Helping Text
				                    </p>
			                    </div>

			                    <div className={`fieldBlk fileType `+ state.value} style={{display: 'none'}}>
				                    <TextField
				                        id="other"
				                        name="other"
				                        label="Other Document Type"
				                        type="text"
				                        className={classes.textField}
				                        value={values.other}
				                        variant="outlined"
				                        {...formik.getFieldProps("other")}
				                    />
				                    <p className="MuiFormHelperText-root MuiFormHelperText-contained">
				                    	Please Specify Other Registration Document Helping Text
				                    </p>
			                    </div>

			                    <div className="fieldBlk  class-submit">
		  					    	<Button 
							            type="submit"
							            disabled={formik.isSubmitting}
		  					    		variant="contained" 
		  					    		color="primary" 
		  					    		className={classes.button}
		  					    	>    
			  					        Upload Document
			  					        {loading && <span className="ml-3 spinner spinner-white"></span>}
			  					    </Button>
			  					    <Button variant="contained" className={classes.button} onClick={viewBusiness}>
			  					        Cancel
			  					    </Button>
		  					    </div>
	  					    </form>
		                </div>
		                <div className="col-md-6 text-center ">
		                	<img src="/media/images/business.jpg" className="certificate" />
		                </div>    
	                </div>	
				    
		        </div>
		    </div>    

		</div>
	</>);

}